@import url('https://fonts.googleapis.com/css?family=Roboto');

* {
  margin: 0;
  padding: 0;
  outline: 0;
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
}

section {
  text-align: center;
  padding: 3em 0;
}

.task_title {
  color: #7f7f7f;
}


table {
  width: 100%;
  border-collapse: collapse;
}

table tbody tr,
table thead tr {
  border-bottom: 1px #e0e0e0 solid;
}
table tbody tr{
  /*background-color: #e9f6ff;*/
